import { ControlLoadError, showDisconnectError, showLoadError } from "../../errors";
import { MessageKind, showAfterUpdatePageMessage, showPageMessage } from "../../alert";
import { ErrorResponse, snakeCaseNamesByCamelCaseMap } from "../../data/types";
import { validateForm } from "./validate";
import { catchAsync, getBaseUrl, isValidIp } from "../../help";
import { loadSettingsData } from "../../data/load";
import { hideGlobalSpinner, showGlobalSpinner } from "../../spinner";
import { selectTabById } from "../../tabpanel";

const SUCCESS_SUBMIT_MSG_TEXT = "Settings saved and applied.";

// const SAVE_FILE_URL = "/api/cmd_conf_c2f";

export function rigSubmitForm(form: HTMLFormElement): void {
    validateForm(form)
        .onSuccess(
            catchAsync(async (e) => {
                e!.preventDefault();
                const errorControl = new ControlLoadError();
                const settings = await loadSettingsData(errorControl);

                let result: undefined | { wlanStaIp?: string; elanIp?: string } | ErrorResponse;
                try {
                    result = await submit(form);
                } catch (err) {
                    showLoadError((err as Error).message);
                    return;
                }

                if (result && "error_descr" in result) {
                    showPageMessage(
                        `Form submit error: ${result.error_descr}`,
                        MessageKind.Danger,
                    );
                    return;
                }

                showAfterUpdatePageMessage(SUCCESS_SUBMIT_MSG_TEXT, MessageKind.Success);

                if (isValidIp(window.location.hostname) && result) {
                    if (window.location.hostname === settings.wlanStaIp && result.wlanStaIp) {
                        redirectToNewHostProccess(
                            `http://${result.wlanStaIp}${window.location.pathname}`,
                        );
                        return;
                    }
                    if (result.elanIp) {
                        redirectToNewHostProccess(
                            `http://${result.elanIp}${window.location.pathname}`,
                        );
                        return;
                    }
                }

                window.location.reload();
            }),
        )
        .onFail((fields) => {
            for (const key in fields) {
                const fieldItem = fields[key];
                if (!fieldItem.isValid) {
                    const tabPaneId = fieldItem.elem.closest(".tab-pane")?.id;
                    if (!tabPaneId) {
                        break;
                    }
                    selectTabById(`#${tabPaneId}`);
                    break;
                }
            }
        });
}

async function submit(
    form: HTMLFormElement,
): Promise<ErrorResponse | { wlanStaIp?: string; elanIp?: string } | undefined> {
    const fields = Array.from(form.querySelectorAll("[data-holds].changed"));
    const snakeCaseNamesMap = snakeCaseNamesByCamelCaseMap();

    const paramsObj = {};
    for (const field of fields) {
        const name = field.attributes.getNamedItem("data-holds")!.value;
        const snakeCaseName = snakeCaseNamesMap.get(name);
        if (!snakeCaseName) {
            continue;
        }
        if (field instanceof HTMLInputElement && field.type === "checkbox") {
            const value = field.checked ? 1 : 0;
            paramsObj[snakeCaseName] = value;
        } else if (field instanceof HTMLInputElement || field instanceof HTMLSelectElement) {
            paramsObj[snakeCaseName] = field.value;
        }
    }
    if (Object.entries(paramsObj).length < 1) {
        return;
    }

    const resp = await fetch(`${getBaseUrl()}/api/set_cnfs.json`, {
        method: "POST",
        headers: {
            "Content-Type": "application/x-www-form-urlencoded",
        },
        body: new URLSearchParams(paramsObj),
    });
    const bodyJson = (await resp.json()) as unknown;

    if (bodyJson && "error_descr" in bodyJson) {
        return bodyJson as ErrorResponse;
    }

    return {
        wlanStaIp: paramsObj["wlan_sta_ip"] as string,
        elanIp: paramsObj["elan_ip"] as string,
    };
}

function redirectToNewHostProccess(newHostname: string): void {
    showGlobalSpinner(60);
    const interval = setInterval(async () => {
        const resp = await fetch(getBaseUrl() + "/api/get_sta.json");
        if (resp.status === 200) {
            clearInterval(interval);
            window.location.href = newHostname;
        }
    }, 1000);

    setTimeout(() => {
        if (interval) {
            hideGlobalSpinner();
            showDisconnectError();
            clearInterval(interval);
        }
    }, 60000);
}
