import { catchAsync, getBaseUrl } from "../help";
import { showGlobalSpinner } from "../spinner";

const RESTART_DEVICE_BTN_ID = "restart-device-btn";
// const RESET_DEVICE_ALERT_BTN_ID = "reboot-device-alert-btn";

const RESTART_URL = `${getBaseUrl()}/api/cmd.json?cmd_abort`;

export function rigRestartDevice(): void {
    document.querySelector(`#${RESTART_DEVICE_BTN_ID}`)?.addEventListener(
        "click",
        catchAsync(async () => {
            const conf = confirm(
                "Confirm restarting OPCB runtime. The connection to the device will be lost temporarily after this action.",
            );
            if (!conf) {
                return;
            }
            await resetDevice();
        }),
    );
    // document
    //   .querySelector(`#${RESET_DEVICE_ALERT_BTN_ID}`)
    //   ?.addEventListener("click", async () => {
    //     await resetDevice();
    //   });
}

async function resetDevice(): Promise<void> {
    showGlobalSpinner(120);
    sessionStorage.setItem("isReboot", "true");

    await fetch(RESTART_URL);
}
