import { showGlobalSpinner } from "../spinner";
import { catchAsync, getBaseUrl } from "../help";

const RESET_DEVICE_BTN_ID = "reset-device-btn";
const RESET_URL = `${getBaseUrl()}/api/cmd.json?cmd_conf_def`;

export function rigResetDevice(): void {
    document.querySelector(`#${RESET_DEVICE_BTN_ID}`)?.addEventListener(
        "click",
        catchAsync(async () => {
            const conf = confirm(
                "Confirm resetting the configuration to the factory values. WARNING: in case of connection loss, access the settings through the Wi-Fi Access Point. Refer to the documentation for further details.",
            );
            if (!conf) {
                return;
            }
            await resetDevice();
        }),
    );
}

async function resetDevice(): Promise<void> {
    showGlobalSpinner(60);
    sessionStorage.setItem("isReboot", "true");

    await fetch(RESET_URL);
}
