import { catchAsync, rigChangeFields } from "../help";
import { DHCPModes, SettingsData, WifiSsidList } from "../data/types";
import { rigSelectPlus } from "../settings/select-plus";
import { ControlLoadError, showLoadError } from "../errors";
import { loadSettingsData, loadStateData, loadWifiSsidNames } from "../data/load";
import { MessageKind, showPageMessage } from "../alert";

// TODO: add image to state

const QUICK_START_CONTAINER_ID = "quick-start-container";
const DHCP_ETHERNET_BTNS_CONTAINER_ID = "ethernet-ip-mode-container";
const ETHERNET_CUSTOM_SETT_ID = "ethernet-custom-settings";
// const WIFI_DHCP_MODE_ID = "wifi-dhcp-mode";
const DHCP_WIFI_CUSTOM_SETT_ID = "wifi-dhcp-custom-sett";
const DHCP_WIFI_BTNS_CONTAINER_ID = "wifi-ip-mode-container";
const WIFI_SSID_SELECT_ID = "wifi-sta-ssid";
const WIFI_SSID_UPDATE_BTN_ID = "wifi-ssid-update-btn";

export async function initQuickSetup(): Promise<void> {
    const container = document.querySelector(`#${QUICK_START_CONTAINER_ID}`) as
        | HTMLElement
        | undefined;
    if (!container) {
        return;
    }
    const form = container.querySelector("form") as HTMLFormElement;

    const errorControl = new ControlLoadError();
    const stateData = await loadStateData(errorControl);
    checkInternetConnection(stateData.inetState);

    rigChangeInternetConnection(container);
    rigChangeEthernetDHCP(container);
    rigChangeWifiIpMode(container);
    await fillFormFields(form, container, errorControl);
    rigSelectPlus();
    rigChangeFields(form);
    rigUpdateWifiSsidList(errorControl);
}

function checkInternetConnection(inetState: number): void {
    if (!inetState) {
        return;
    }
    showPageMessage(
        `Device has working internet connection by the Ethernet link.
    Submit this form to disable the WiFi Access Point.`,
        MessageKind.Success,
    );
}

function rigChangeInternetConnection(container: HTMLElement): void {
    const internetOptionsContainer = container.querySelector(
        "#quick-start-internet-methods",
    ) as HTMLElement;

    internetOptionsContainer.addEventListener("change", () => {
        const activeOption = (
            internetOptionsContainer.querySelector(
                "input[name='quick-start-radio-btns']:checked",
            ) as HTMLInputElement
        ).value;
        container.classList.remove("ethernet", "wifi");
        container.classList.add(activeOption);
    });
}

function rigChangeEthernetDHCP(container: HTMLElement): void {
    const radioBtnsContainer = container.querySelector(
        `#${DHCP_ETHERNET_BTNS_CONTAINER_ID}`,
    ) as HTMLSelectElement;

    const customSettEl = container.querySelector(`#${ETHERNET_CUSTOM_SETT_ID}`);

    radioBtnsContainer.addEventListener("change", () => {
        const value =
            (
                container.querySelector(
                    "input[name='ethernet-ip-mode-radio']:checked",
                ) as HTMLInputElement
            ).value === "automatic"
                ? DHCPModes.Automatic
                : DHCPModes.Custom;

        if (value === DHCPModes.Automatic) {
            customSettEl?.classList.add("d-none");
            return;
        }
        customSettEl?.classList.remove("d-none");
    });
}

function rigChangeWifiIpMode(container: HTMLElement): void {
    const radioBtnsContainer = container.querySelector(
        `#${DHCP_WIFI_BTNS_CONTAINER_ID}`,
    ) as HTMLSelectElement;

    const customSettEl = container.querySelector(`#${DHCP_WIFI_CUSTOM_SETT_ID}`);

    radioBtnsContainer.addEventListener("change", () => {
        const value =
            (
                container.querySelector(
                    "input[name='wifi-ip-mode-radio']:checked",
                ) as HTMLInputElement
            ).value === "automatic"
                ? DHCPModes.Automatic
                : DHCPModes.Custom;

        if (value === DHCPModes.Automatic) {
            customSettEl?.classList.add("d-none");
            return;
        }
        customSettEl?.classList.remove("d-none");
    });
}

function rigUpdateWifiSsidList(errorControl: ControlLoadError): void {
    const updateBtn = document.querySelector(`#${WIFI_SSID_UPDATE_BTN_ID}`) as HTMLElement;

    updateBtn.addEventListener(
        "click",
        catchAsync(async () => {
            updateBtn.setAttribute("disabled", "disabled");
            updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
            updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
            await fillWifiSsidNames(errorControl);
            updateBtn.removeAttribute("disabled");
        }),
    );
}

function setEthernetIpMode(container: HTMLElement, mode: DHCPModes): void {
    const radioBtnsContainer = container.querySelector(
        `#${DHCP_ETHERNET_BTNS_CONTAINER_ID}`,
    ) as HTMLSelectElement;

    if (mode === DHCPModes.Automatic) {
        (radioBtnsContainer.querySelector("#etherbet-auto-radio") as HTMLInputElement).checked =
            true;
        return;
    }
    (radioBtnsContainer.querySelector("#ethernet-manual-radio") as HTMLInputElement).checked =
        true;
    radioBtnsContainer.dispatchEvent(new Event("change"));
}

function setWifiIpMode(container: HTMLElement, mode: DHCPModes): void {
    const radioBtnsContainer = container.querySelector(
        `#${DHCP_WIFI_BTNS_CONTAINER_ID}`,
    ) as HTMLSelectElement;

    if (mode === DHCPModes.Automatic) {
        (radioBtnsContainer.querySelector("#wifi-auto-radio") as HTMLInputElement).checked = true;
        return;
    }
    (radioBtnsContainer.querySelector("#wifi-manual-radio") as HTMLInputElement).checked = true;
    radioBtnsContainer.dispatchEvent(new Event("change"));
}

async function fillFormFields(
    form: HTMLFormElement,
    container: HTMLElement,
    errorControl: ControlLoadError,
): Promise<void> {
    try {
        const data = await loadSettingsData(errorControl);
        fillGeneralFields(form, data);
        await fillWifiSsidNames(errorControl);
        setEthernetIpMode(container, data.elanDhcp);
        setWifiIpMode(container, data.wlanStaDhcp);
    } catch (e) {
        const text = (e as Error).message;
        showLoadError(text);
    }
}

function fillGeneralFields(form: HTMLFormElement, data: SettingsData): void {
    const elems = Array.from(form.querySelectorAll("[data-holds]"));
    for (const el of elems) {
        const name = el.attributes.getNamedItem("data-holds")!.value;
        if (!data.hasOwnProperty(name)) {
            throw new Error(`Unknown field name: ${name}`);
        }
        const val = data[name] as string;
        if (el instanceof HTMLInputElement && el.type === "checkbox") {
            el.checked = !!data[name];
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLInputElement) {
            el.setAttribute("value", val);
            el.dispatchEvent(new Event("input"));
            el.dispatchEvent(new Event("change"));
        } else if (el instanceof HTMLSelectElement) {
            if (el.classList.contains("select-with-plus")) {
                const optionEl = document.createElement("option");
                optionEl.value = val;
                optionEl.text = val;
                optionEl.selected = true;
                el.appendChild(optionEl);
            }
            el.value = val;
            el.dispatchEvent(new Event("change"));
        } else {
            el.innerHTML = val;
        }
    }
}

async function fillWifiSsidNames(errorControl: ControlLoadError): Promise<void> {
    const list = (await loadWifiSsidNames(errorControl)).sort((a, b) =>
        a.signal < b.signal ? 1 : -1,
    );

    fillWifiSsidSelect(WIFI_SSID_SELECT_ID, list);

    const updateBtn = document.querySelector(`#${WIFI_SSID_UPDATE_BTN_ID}`) as HTMLElement;
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
    updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
}

function fillWifiSsidSelect(selectId: string, list: WifiSsidList[]): void {
    const listEl = document.querySelector(`#${selectId}`) as HTMLSelectElement;
    const currentSsid = listEl.value;

    listEl.innerHTML = "";

    const defaultOption = document.createElement("option");
    defaultOption.value = "";
    defaultOption.text = "----";
    listEl.appendChild(defaultOption);

    const addOption = (ssid: string, signal?: number): void => {
        const option = document.createElement("option");
        option.value = ssid;
        option.text = signal ? `${ssid} (${signal} dBm)` : ssid;
        option.selected = ssid === currentSsid;
        listEl.appendChild(option);
    };
    const ssids = list.map((l) => l.ssid);
    if (!ssids.includes(currentSsid)) {
        addOption(currentSsid);
    }

    for (const item of list) {
        addOption(item.ssid, item.signal);
    }
}
