import { showGlobalSpinner } from "../spinner";
import { catchAsync, getBaseUrl } from "../help";

const REBOOT_DEVICE_BTN_ID = "reboot-device-btn";
const REBOOT_URL = `${getBaseUrl()}/api/cmd.json?cmd_abort_all`;

export function rigRebootDevice(): void {
    document.querySelector(`#${REBOOT_DEVICE_BTN_ID}`)?.addEventListener(
        "click",
        catchAsync(async () => {
            const conf = confirm(
                "Confirm reboot. The connection to the device will be lost for several minutes after this action.",
            );
            if (!conf) {
                return;
            }
            await rebootDevice();
        }),
    );
}

async function rebootDevice(): Promise<void> {
    showGlobalSpinner(60);
    sessionStorage.setItem("isReboot", "true");

    await fetch(REBOOT_URL);
}
